import {ATextInput} from "./abstract/a-text-input";
import jQuery = require("jquery");

/**
 * @classdesc Input usado na edição de textos
 *
 * <img src="../../doc_assets/textinput_example.png">
 *
 * @example
 * 
 * import {TextInput} from "mvcomponents/input";
 * 
 * let codigo:TextInput = new TextInput();
 * codigo.setLabel("codigo.");
 * 
 * let nome:TextInput = new TextInput()
 * nome.setLabel("nome");
 * 
 * this.append(codigo).append(nome);
 * 
 * @export
 * @extends {ATextInput}
 * @memberof module:mvcomponents/input
 */
export class TextInput extends ATextInput{
	constructor(value: string = "") {
		super("text", value);
		this.addStyleName("TextInput");
	}
}
